@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#f9cb0d}.debarras{font-family:'Archivo Black', sans-serif;font-size:2.5rem}.pics{width:90%;height:60%}.NavBar{cursor:pointer}.cards{display:inline-block;width:100%;max-width:350px;margin:20px;cursor:pointer;transition:all 0.3s ease;text-align:center;background:#e6e6e6;box-shadow:0 6px 10px -5px rgba(0,0,0,0.2);border-radius:15px}.cards:hover{transform:translateY(-5px);box-shadow:0 20px 20px -15px rgba(0,0,0,0.3)}.pics{width:288px;height:200px;border-radius:5px}.form{padding-top:5%}.buttondet{background-color:#1e90ff;color:white}.containermax{height:400px;width:100%}.main-footer{position:-webkit-sticky;position:sticky;margin-top:0px;padding-top:150px;text-align:center}

